import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import axios from 'axios';
import { useParams } from "react-router-dom";
import Header from "./Header";

export default function Password() {
    const ids = useParams();
    
    const userid = ids.id;
    console.log(userid);
    const [detail, setDetail] = useState({
        password: "",
        confirmPassword: ""
        
      });
   
      const { password, confirmPassword} = detail;

    const onInputChange = e => {
        setDetail({ ...detail, [e.target.name]: e.target.value });
    };
    const validationSchema = Yup.object().shape({
        password: Yup.string()
            .required('Password is required')
            .min(6, 'Password must be at least 6 characters'),
        confirmPassword: Yup.string()
            .required('Confirm Password is required')
            .oneOf([Yup.ref('password')], 'Passwords must match')

    });
    const formOptions = { resolver: yupResolver(validationSchema) };

    // get functions to build form with useForm() hook
    const { register, handleSubmit, formState } = useForm(formOptions);
    const { errors } = formState;
    console.log(errors);

   function onSubmit(e) {
   
        console.log(detail);
 
        axios.put("http://localhost:5000/updatePassword/" + userid, detail, {
            "headers": {
                "content-type": "application/json",
            },
        } )
        reset()
        // history.push("/list");
    };

    function reset() {
        setDetail({ password: "", confirmPassword: ""})
    }

  return (
    <div>
        <Header/>
        <section className="Preferred_Password">
  <div className="container">
    <div id="pass-page" className="pass_page pt-5">      
        <div className="form">
          <div className="pass-title pb-2">
            <p>Preferred Password</p>
          </div>
          {!errors.password  && !errors.confirmPassword && formState.isSubmitted && (
                           <div class="alert alert-success alert-dismissible">
                           <a href="#" class="close" data-dismiss="alert" aria-label="close">&times;</a>
                           <strong>Success!</strong> Indicates a successful or positive action.
                         </div>
                            
                        )}
            <form className="pass_form" onSubmit={handleSubmit(onSubmit)}> 
             <a href="#"><i className="bi bi-eye-slash-fill pass_icn"></i></a> 
              <input type="password" placeholder="Password" className="in_pass" name ="password"  value={password}{...register('password')} className={`form-control ${errors.password ? 'is-invalid' : ''}`} onChange={e => onInputChange(e)} />
              <div className="invalid-feedback">{errors.password?.message}</div> 
              <a href="#"><i className="bi bi-eye-slash-fill con_pass_icn"></i></a> 
               <input type="password" placeholder="Conform Password" className="con_pass" name ="confirmPassword"  value={confirmPassword} {...register('confirmPassword')} className={`form-control ${errors.confirmPassword ? 'is-invalid' : ''}`} onChange={e => onInputChange(e)}/>
               <div className="invalid-feedback">{errors.confirmPassword?.message}</div>
              <button className="Conform" type ="submit">Conform</button>
            </form>
        </div>
     </div>
  </div>
</section>
      
    </div>
  )
}
