import React, { useState,props,useEffect } from 'react';
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import CheckoutForm from './CheckoutForm';
import { useParams } from 'react-router-dom';
// import './index.css';

// Make sure to call `loadStripe` outside of a component’s render to avoid
// recreating the `Stripe` object on every render.
const stripePromise = loadStripe("pk_test_51IFCZMCgALYZd2y9nucePviKKe8bmpoYVZ9Hui3oEGxHXFBTZoPMQcnEkTg8iYDGxT0lUIaV56a8vVtGhtOK42vJ00ZVSBk1g4");

const successMessage = () => {
 
  return (
    <div className="success-msg">
      <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-check2" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
        <path fill-rule="evenodd" d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z" />
      </svg>
      <div className="title">Payment Successful</div>
    </div>
  )
}

const cart = () => {
  return (<React.Fragment>
   
  </React.Fragment>)
}

function App() {
  const { id } = useParams();
  console.log(id);

  const [newdata, setNewdata] = useState();
  const [paymentCompleted, setPaymentCompleted] = useState(false);


  useEffect(() => {
    fetch('http://localhost:5000/edit/'+id)
      .then((res) => res.json())
      .then((data) => {
        setNewdata(data[0].payment);
      })
      .catch((err) => {
        console.log(err);
      });
      
  }, []);
  console.log(newdata);
  return (
    <div className="container">
      

      <div className="row s-box">
        {paymentCompleted ? successMessage() : <React.Fragment>
          <div className="col-md-5 order-md-2 mb-4">
            {cart()}
          </div>
          <div className="col-md-7 order-md-1">
            <Elements stripe={stripePromise}>
              <CheckoutForm amount={newdata} setPaymentCompleted={setPaymentCompleted} />
            </Elements>
          </div>
        </React.Fragment>}
      </div>

    </div>
  );
}

export default App;