
import React, { useState ,useEffect} from "react";
import Navbar from './Navbar';
import Sidebar from './Sidebar';

import Stripeapp from '../stripe/App';
import { Link } from 'react-router-dom';

export default function Subscription() {
    const [data, setData] = useState([]);
    const [id, setId] = useState([]);

    console.log(process.env.REACT_APP_CLIENT_ID); console.log(process.env.REACT_APP_KEY);
              useEffect(() => {
                fetch('http://localhost:5000/getData')
                  .then((res) => res.json())
                  .then((data) => {
                    setData(data);
                  })
                  .catch((err) => {
                    console.log(err);
                  });
              }, []);
             
            //   console.log(data[0].id);
    return (
        <div className="sb-nav-fixed">

            <Navbar />

            <div id="layoutSidenav">

                <Sidebar />


                <div id="layoutSidenav_content">
                    <div className="pt-4 pb-5">
                        <main>
                            <div className="containar-fluid px-5">

                                <div className="subscription" style={{ marginLeft: '100px' }}>

                                    <div className="row subs_main_row d-flex justify-content-center">



                                        <div className="row ">
                                            <div className="col-md-4">
                                                <p className="subs_head mb-0"> Subscription Types</p>
                                            </div>
                                            <div className="col-md-4">
                                                <p className="subs_head mb-0">Payment</p>
                                            </div>
                                        </div>

                                        
                                       {data.map((item) => (
                                          <div className="row subs_line">
                                            <div className="col-md-4">
                                                <p className="type_pra mb-0">{item.type}</p>
                                            </div>
                                            <div className="col-md-4">
                                                <p className="pay_pra mb-0">{item.payment}{item.currency}</p>
                                            </div>
                                          
                                            <div className="col-md-4 text-center">
                                                <Link className="btn btn-primary"  to={{ pathname: `/appstripe/${item.id}`}}>Pay</Link>
                                            </div>
                                        </div>
                                         )) }

                                       

                                    </div>

                                </div>

                            </div>
                        </main>
                    </div>
                </div>
            </div>
        </div>
    )
}
